class NavbarController {
  constructor($scope, $log, $translate, $mdSidenav, $mdToast) {
    this.firebaseUrl = 'https://travelerman.firebaseio.com';
    this.$scope     = $scope;
    this.$log       = $log;
    this.$translate = $translate;
    this.$mdSidenav = $mdSidenav;
    this.$mdToast   = $mdToast;

    this.isSigned        = false;

    this.userName        = '';
    this.userAvater      = '';
    this.currentLanguage = '';

    this.getLanguage();

  }
  getUser(){
    let instance = new Firebase(this.firebaseUrl);
    instance.authWithOAuthPopup('github', (error, authData) => {
      this.userName   = authData.github.displayName;
      this.userAvater = authData.github.profileImageURL;
      this.isSigned   = true;
      this.$scope.$apply();
      this.$log.debug(`Login as ${this.userName}`);
      this.$mdToast.show(this.$mdToast.simple()
        .textContent(`Login as ${this.userName}`));
    });
  };
  logOut(){
    let instance = new Firebase(this.firebaseUrl);
    instance.unauth();
    this.isSigned = false;
  }
  getLanguage(){
    this.currentLanguage = this.$translate.use();
    this.$log.debug(`Set ${this.currentLanguage}`)
  };
  changeLanguage = lang => {
    this.$translate.use(lang);
    this.currentLanguage = this.$translate.use();
    this.$log.debug(`Changed ${this.currentLanguage}`);
  };
  toggle(param) {
    this.$mdSidenav(param).toggle()
      .then(() => {
        this.$log.debug(`${param} sidenav is toggled`);
      })
  };
}

NavbarController.$inject = [
  '$scope',
  '$log',
  '$translate',
  '$mdSidenav',
  '$mdToast'
];

export default NavbarController;



